import { api } from '@/lib/api';
import { Club } from '@/types';
import { notFound } from 'next/navigation';
import { ClubForm } from '@/components/club-form';
import { ArrowLeft, Trophy } from 'lucide-react';
import Link from 'next/link';

// Nota: Esta página es un Server Component (async function) para la obtención de datos
interface ClubEditarPageProps {
    params: {
        clubId: string; // El ID del club viene de la carpeta [id]
    };
}

async function getClubData(id: string): Promise<Club | null> {
    try {
        // Hacemos la llamada a la API solo si el ID no es undefined/null
        const club = await api.getClubPorId(id);
        return club;
    } catch (error) {
        return null;
    }
}

export default async function ClubEditarPage({ params }: ClubEditarPageProps) {
    // La desestructuración estándar y correcta en Next.js Server Components.
    const { clubId } = params;

    // Cláusula de guardia para asegurar que el ID es un valor truthy (no undefined, no null, no '')
    if (!clubId) {
        // Esto debería atrapar cualquier error de mapeo del router si el ID no llega
        return notFound();
    }

    // Usamos el ID de forma segura
    const club = await getClubData(clubId);

    if (!club) {
        // Si el club no existe en la API, mostramos 404
        notFound();
    }

    return (
        <div className="min-h-screen bg-slate-50 dark:bg-slate-950">
            <header className="bg-white dark:bg-slate-900 border-b border-slate-200 dark:border-slate-800 sticky top-0 z-10">
                <div className="container mx-auto px-4 h-16 flex items-center justify-between">
                    <div className="flex items-center gap-2">
                        <div className="bg-blue-600 p-2 rounded-lg">
                            <Trophy className="h-5 w-5 text-white" />
                        </div>
                        <h1 className="text-xl font-bold bg-gradient-to-r from-blue-600 to-indigo-600 bg-clip-text text-transparent hidden sm:block">
                            Gestión Fútbol
                        </h1>
                    </div>
                </div>
            </header>
            <div className="max-w-6xl mx-auto py-8 px-4">
                <div className="mb-8 flex items-center justify-between">
                    <Link
                        href="/clubes"
                        className="inline-flex items-center text-sm font-medium text-slate-500 hover:text-slate-900 dark:text-slate-400 dark:hover:text-slate-100 transition-colors py-2"
                    >
                        <ArrowLeft className="mr-2 h-4 w-4" />
                        Volver al Listado de Clubes
                    </Link>
                </div>

                <h1 className="text-3xl font-bold mb-6 text-slate-900 dark:text-white">
                    Editar Club: {club.nombre}
                </h1>

                <ClubForm club={club} />
            </div>
        </div>
    );
}